const candidate = Array(45).fill().map((v, i) => i + 1)
// 1 ~ 45 ÍπåÏßÄ ÎΩëÎäî ÏΩîÎìú
// Array(45): length Í∞Ä 45Í∞úÏù∏ Îπà Î∞∞Ïó¥ ÎßåÎì§Í∏∞,
// fill(): Îπà Î∞∞Ïó¥ ÏïàÏóê undefined Î°ú 45Í∞ú Ï±ÑÏö∞Í∏∞,
// map(): ÎßàÏßÄÎßâÏúºÎ°ú map ÏùÑ ÌÜµÌï¥ index Ïóê 1Ïî© ÎçîÌïòÍ∏∞

console.log(candidate) // [ 1, 2, 3 ... 45 ] ÏÉùÏÑ±

const shuffle = []
while (candidate.length > 0) {
  // Ï°∞Í±¥(candidate.length) ÏûêÏ≤¥Í∞Ä ÏïåÏïÑÏÑú Í≥ÑÏÜç Î≥ÄÌï¥Ï£ºÍ∏∞ ÎñÑÎ¨∏Ïóê i++, i-- Îì±Ïù¥ ÌïÑÏöî x
  const random = Math.floor(Math.random() * candidate.length) // Î¨¥ÏûëÏúÑ Ïù∏Îç±Ïä§ ÎΩëÍ∏∞
  const spliceArray = candidate.splice(random, 1) // ÎΩëÏùÄ Í∞íÏùÄ Î∞∞Ïó¥Ïóê ÏûàÏùå
  const value = spliceArray[0] // Î∞∞Ïó¥Ïóê Îì§Ïñ¥ÏûàÎäî Í∞íÏùÑ Í∫ºÎÇ¥Ïñ¥
  shuffle.push(value) // shuffle Î∞∞Ïó¥Ïóê ÎÑ£Í∏∞ 
}
// ÏûëÎèôÏõêÎ¶¨: 45Í∞úÏùò Î¨∂ÏùåÏù¥ ÏûàÏúºÎ©¥ ÎûúÎç§ÏúºÎ°ú ÌïòÎÇòÏî© ÎΩëÏïÑÏÑú ÏôÑÏ†ÑÌûà ÎûúÎç§ÏúºÎ°ú ÏÑûÏù∏ Î∞∞Ïó¥ ÌïòÎÇòÎ•º ÏÉàÎ°ú Îã§Ïãú ÎßåÎì§Ïñ¥ÎÉÑ
console.log(shuffle)

const winBalls = shuffle.slice(0, 6).sort((a, b) => a - b) 
// 1. slice(0, 6): 0Î≤àÏß∏ Î∂ÄÌÑ∞ ÌïòÏó¨ 6Í∞úÏùò Ïà´Ïûê ÏûòÎùºÎÇ¥Í∏∞ (ÏõêÎ≥∏ shuffle[] ÏùÄ ÌõºÏÜêÎêòÏßÄ x)
// 2. sort(): ÏûëÏùÄÏà´Ïûê => ÌÅ∞Ïà´Ïûê Ïàú(Ïò§Î¶ÑÏ∞®Ïàú) ÏúºÎ°ú Ï†ïÎ†¨
const bonus = shuffle[6] // 7Î≤àÏß∏ Ïà´Ïûê: Î≥¥ÎÑàÏä§ Ïà´Ïûê

console.log(winBalls, bonus)